.tromboneLink {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    width: 220px;
    margin: 15px;
    padding: 5px;
    background: white;
    border: 1px solid #9e9e9e;
    border-radius: 5px;
    z-index: 1;
    -webkit-transition: all 0.4s ease-in-out;
    -o-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
    text-decoration: none;
}
.tromboneLink:hover {
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}
.tromboneList ul {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.trombone {
    position: relative;
    white-space: nowrap;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    overflow: hidden;
}
/* React animations for changes in trombone search */
.tromboneAnimate-enter {
    opacity: 0.01;
}
.tromboneAnimate-enter.tromboneAnimate-enter-active {
    opacity: 1;
    -webkit-transition: all 1s ease-in;
    -o-transition: all 1s ease-in;
    transition: all 1s ease-in;
}
.tromboneAnimate-leave {
    opacity: 1;
}
.tromboneAnimate-leave.tromboneAnimate-leave-active {
    opacity: 0.01;
    -webkit-transform: translate(0px, 5px);
        -ms-transform: translate(0px, 5px);
            transform: translate(0px, 5px);
    -webkit-transition: all 0.5s ease-in;
    -o-transition: all 0.5s ease-in;
    transition: all 0.5s ease-in;
}


