#backToTop {
    position: fixed;
    bottom: 0;
    right: 10%;
    z-index: 99;
    width: 50px;
    height: 50px;
    background-color: white;
    border-radius: 5px 5px 0px 0px;
    -webkit-box-shadow: 0px 1px 5px 2px rgb(53, 87, 87);
            box-shadow: 0px 1px 5px 2px rgb(53, 87, 87);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    opacity: 0;
    -webkit-transition: 1s;
    -o-transition: 1s;
    transition: 1s;
}
#toTop {
    position: relative;
    padding: 10px;
    background-color: rgb(197, 195, 195);
    border-radius: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    cursor: pointer;
    -webkit-transition: 0.2s;
    -o-transition: 0.2s;
    transition: 0.2s;
}
#toTop:hover {
    background-color: grey;
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
}
#arrow {
    width: 20px;
}


