.container {
    min-height: 400px;
    width: 75%;
    margin: auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.link {
    margin: 5px 10px;
    padding: 3px;
    color: rgb(84,135,135);
    font-weight: bold;
    text-decoration: none;
    display: inline-block;
    font-size: 18px;
    -webkit-transition: -webkit-transform 0.2s ease-in-out;
    transition: -webkit-transform 0.2s ease-in-out;
    -o-transition: transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out;
    transition: transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}
.link:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
    color: rgb(44, 71, 71);
}
.bibliography li {
    text-align: left;
    margin: 20px 0px;
}
.contactLink {
    color: rgb(84,135,135);
}
.contactLink:hover {
    color: rgb(44, 71, 71);
}
#printScreen {
    border: none;
    cursor: pointer;
}
#backLink {
    margin-right: 30px;
}
#detailsLinks {
    width: 100%;
    clear: both;
}
#minLinks {
    width: 100%;
    text-align: center;
}
#adminInstruments {
    margin-top: 35px;
}



